<template>
    <div id="career" class="career">
        <div v-if="careers.length>0" class="container">
            <ul class="responsive-table">
                <li class="table-header">
                    <div class="col col-1">Bank Name</div>
                    <div class="col col-2">Job Title</div>
                    <div class="col col-3">Pay</div>
                    <div class="col col-4">Job Description</div>
                    <div class="col col-5">Contact Details</div>
                </li>
                <li v-for="(i,key) of careers" :key="key" class="table-row">
                    <div class="col col-1" data-label="Bank Name">{{i.name}}</div>
                    <div class="col col-2" data-label="Job Title">{{i.title}}</div>
                    <div class="col col-3" data-label="Pay">{{i.pay}}</div>
                    <div class="col col-4" data-label="Job Description">{{i.description}}</div>
                    <div class="col col-5" data-label="Contact Details">{{i.contact}}</div>
                </li>
            </ul>
        </div>
        <h1 style="margin-top:10%" v-else>You have no Careers</h1>
    </div>
</template>

<script>
    import http from '../../services/http'
    export default {
        data() {
            return {
                careers: []
            }
        },
        async created() {
            this.$emit('title', 'Careers');
            let result = await http.getAllCareers();
            for (let i in result.careers) {
                for (let j of result.careers[i].result) {
                    j.name = result.careers[i]._id;
                    this.careers.push(j);
                }
            }
        },
        mounted() {
            document.getElementById('career').style.marginTop = '' + document.getElementById('toolbar').offsetHeight + 'px';
            // console.log(window.getComputedStyle(document.getElementById('hello')).marginTop);
        }
    }
</script>

<style scoped>
    .career {
        margin-top: 60px;
    }
    .container {
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
        padding-left: 10px;
        padding-right: 10px;
    }
    h2 {
        font-size: 26px;
        margin: 20px 0;
        text-align: center;
    }
    h2 small {
        font-size: 0.5em;
    }
    .responsive-table li {
        border-radius: 3px;
        padding: 25px 30px;
        display: flex;
        justify-content: space-between;
        margin-bottom: 25px;
    }
    .responsive-table .table-header {
        background-color: #95a5a6;
        font-size: 14px;
        text-transform: uppercase;
        letter-spacing: 0.03em;
    }
    .responsive-table .table-row {
        background-color: #ffffff;
        box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1);
    }
    .responsive-table .col-1 {
        flex-basis: 10%;
    }
    .responsive-table .col-2 {
        flex-basis: 40%;
    }
    .responsive-table .col-3 {
        flex-basis: 25%;
    }
    .responsive-table .col-4 {
        flex-basis: 25%;
    }
    @media all and (max-width: 767px) {
        .responsive-table .table-header {
            display: none;
        }
        .responsive-table li {
            display: block;
        }
        .responsive-table .col {
            flex-basis: 100%;
        }
        .responsive-table .col {
            display: flex;
            padding: 10px 0;
        }
        .responsive-table .col:before {
            color: #6c7a89;
            padding-right: 10px;
            content: attr(data-label);
            flex-basis: 50%;
            text-align: right;
        }
    }
</style>


